.pop-up {
  position: fixed;
  top: 0;
  width: 100%;
  height: 100%;
  background: url('../img/pop-up-bg.png');
  z-index: 99;
  transition: all 0.8s;
  transform: translateY(-110%);
  overflow-x: hidden;
  @include r(767) {
    padding: 0 15px; }
  &.active {
    transform: translateY(0); }
  &:before {
    content: '';
    background: url('../img/step.png') no-repeat;
    background-size: contain;
    margin-top: -20px;
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 6;
    @include r(767) {
      margin-left: -15px; } }
  &:after {
    content: '';
    background: url('../img/step.png') no-repeat;
    background-size: contain;
    margin-top: -20px;
    position: absolute;
    bottom: -45px;
    width: 100%;
    height: 100%;
    transform: rotate(180deg);
    z-index: 6;
    @include r(767) {
      margin-left: -15px; } }
  .row {
    position: relative;
    z-index: 99; }
  .close {
    position: absolute;
    top: 0px;
    left: 20px;
    width: 40px;
    height: 30px;
    padding-top: 15px;
    &:hover {
      span {
        background: $moss;
        &:before {
          background: $moss; } } }
    span {
      width: 35px;
      height: 2px;
      background: $black;
      display: block;
      transform: rotate(45deg);
      transition: all 0.3s;
      &:before {
        content: '';
        position: absolute;
        left: 0;
        transform: rotate(90deg);
        width: 35px;
        height: 2px;
        background: $black;
        transition: all 0.3s; } } }
  li {
    a {
      font-family: 'Literaturnaya';
      font-size: 26px;
      color: $black;
      &:hover {
        color: #a5880d;
        border-bottom: 1px solid #a5880d; } } }
  h4 {
    font-size: 26px;
    line-height: 1.54;
    letter-spacing: 1px;
    text-align: center;
    color: $greyish-brown;
    font-weight: 300;
    padding-bottom: 40px; } }
